<template>
  <div id="shortLink" class="scrollTouch" :class="{hasHeader: $utils.showNavBar()}">
    <HeaderNav v-if='$utils.showNavBar()'></HeaderNav>
    <div class="shortLink">
      <div class="title flex-row">什么是我的邀请链接</div>
      <div class="flex-row titleSec">将邀请链接发送给好友，好友点击链接注册，就算被你邀请成功了！</div>
      <div class="your_linik">
        <div class="link_title redColor">点击复制您的邀请链接</div>
        <div class="linkBox btn" @click="copyText" :data-clipboard-text="shortLinkText">{{shortLink}}</div>
      </div>
      <div class="copy_button flex-row btn" @click="setShare" ref="copyButton" :data-clipboard-text="shortLinkText">分享给好友</div>
    </div>
    <ShareCommon v-if="showShare" :shareParams='shareParams' @closeShare='closeShare'></ShareCommon>
    <IndexShare @shareSuccess='shareSuccess' v-if="$route.path === '/short_link'" tag='index' :appShare='appShare'></IndexShare>
  </div>
</template>
<script>
import HeaderNav from '@/components/task/header-nav'
import IndexShare from '@/components/share/IndexShare'
import ShareCommon from '@/components/share/shareCommon'
import {mapState, mapMutations} from 'vuex'
import Clipboard from 'clipboard'
export default {
  components: {
    HeaderNav,
    IndexShare,
    ShareCommon
  },
  data () {
    return {
      shortLink: '', // 短链接
      shareLink: '', // 前端分享链接
      shortLinkText: '', // 分享文本
      showShare: false,
      shareParams: {},
      appShare: {
        count: 0,
        path: '/share_page/' + Math.ceil(3 * Math.random())
      }
    }
  },
  computed: mapState({
    commonState: state => state.common
  }),
  methods: {
    ...mapMutations([
      'setShowShare',
      'set_shareSuccessObj',
      'set_shareType'
    ]),
    // 微信组件
    shareSuccess (e) {
      this.closeShare()
    },
    // 显示app分享
    showAppShare () {
      this.appShare.count++
    },
    // 获取短链接
    getShortLink () {
      let cacheShortLink = this.$utils.cache('shortLink')
      if (!cacheShortLink) {
        this.requestUrl()
      } else {
        cacheShortLink = JSON.parse(cacheShortLink)
        if (new Date().getTime() > cacheShortLink.timestamp) {
          this.requestUrl()
        // 此处使用缓存
        } else {
          this.shortLink = cacheShortLink.data[0].url_short
          this.shortLinkText = `我是${this.commonState.userInfo.nickname}，邀请你一起看文章，赚豪车赚洋房；↓↓↓点击查看↓↓↓${this.shortLink}`
        }
      }
    },
    requestUrl () {
      let params = {
        download_path: '/download/index.html',
        invite: this.commonState.userInfo.invitation
      }
      
      if (this.$config.source_style >= 6) {
        this.shareLink = `${this.$config.currentWebUrl}?${this.$qs.stringify(params)}`
      } else {
        this.shareLink = `${location.origin}?${this.$qs.stringify(params)}`
      }
      this.$api.get('SHORT_URL', {url: this.shareLink}, res => {
        this.shortLink = res.data[0].url_short
        this.shortLinkText = `我是${this.commonState.userInfo.nickname}，邀请你一起看文章，赚豪车赚洋房；↓↓↓点击查看↓↓↓${this.shortLink}`
        if (res.data && res.data[0].url_short) {
          this.$utils.cache('shortLink', JSON.stringify({
            timestamp: new Date().getTime() + 3600 * 24 * 1000,
            data: res.data
          }))
        }
      })
    },
    setShare () {
      // let data  = {
      //   btn_name: 'earn_invite_btn_url_share'
      // }
      // this.$api.post('TOTAL_CLICKBTN', data, res => {})
      if (this.$config.source_style >= 6) {
        this.showAppShare()
      } else if (this.$utils.isWx()) {
        this.showShare = true
      } else {
        this.copyText()
      }
    },
    closeShare () {
      this.showShare = false
    },
    // 初始化短链接
    init () {
      this.getShortLink()
    },
    // 复制链接
    copyText () {
      if (Clipboard.isSupported()) {
        let clb = new Clipboard('.btn')
        clb.on('success',  (e) => {
          if (this.$route.query.tag) {
            this.$api.get('CALLBACK_REWARD', {tag: this.$route.query.tag}, res => {
              if (res.data.coin) {
                let data = res.data
                // 下列弹框仅适用于晒收益奖励,若有其余tag需重新设置
                data.type = this.commonState.shareArc[this.$route.query.tag]
                data.tipText = '复制链接成功，快发送给好友吧~'
                this.set_shareType(16)
                this.set_shareSuccessObj({
                  type: 16,
                  shareSuccessObj: data
                })
                this.setShowShare(true)
              } else {
                this.$toast({
                  message: '复制链接成功，快发送给好友吧~',
                  duration: 1500
                })
              }
            })
          } else {
            this.$toast({
              message: '邀请成功，快发送给好友吧~',
              duration: 1500
            })
          }
          clb.destroy()
        })
        clb.on('error',  (e) => {
          this.$toast({
            message: '复制链接失败了',
            position: 'middle',
            duration: 1500,
            iconClass: 'icon icon-success'
          })
          clb.destroy()
        })
      }
    }
  },
  watch: {
    'commonStata.login_box': function (flag) {
      if (!flag && this.commonState.login_bol) {
        this.init()
      }
    }
  },
  created () {
    this.init()
  }
}
</script>
<style lang="scss" scoped>
#shortLink{
  height: 100%;
  box-sizing: border-box;
  font-size: 30px;
  .title{
    padding: 50px 40px;
  }
  .titleSec{
    padding: 0 130px;
    line-height: 1.5;
  }
  .your_linik{
    width: 690px;
    height: 188px;
    border-radius: 15px;
    margin: 88px auto 64px;
    box-shadow: 2px 4px 10px rgba(20, 20, 20, .32);
    text-align: center;
    .link_title {
      padding: 30px 0 20px;
    }
    .linkBox{
      width: 510px;
      height: 52px;
      line-height: 52px;
      background: #f5f5f5;
      margin: 0 auto;
      border-radius: 15px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      font-size: 24px;
      color: #222;
    }
  }
  .copy_button{
    height: 80px;
    color: #fff;
    width: 690px;
    margin: 0 auto;
    background-image: linear-gradient(135deg, #e75d58,#fb7857);
    border-radius: 15px;
  }
}
</style>



